import React, { useState } from 'react'
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';


function Samo(props) {
    // console.log('props', props)
    const [state, setState] = useState({
        src: null,
        crop: {
            unit: '%',
            width: 30,
            aspect: 16 / 9,
        },
    })

    const [imageRef, setImageRef] = useState(null)

    const onSelectFile = e => {
        if (e.target.files && e.target.files.length > 0) {
            const reader = new FileReader();
            reader.addEventListener('load', () =>
                setState(state => ({ ...state, src: reader.result }))
            );
            reader.readAsDataURL(e.target.files[0]);
        }
    };
    // If you setState the crop in here you should return false.
    const onImageLoaded = image => {
        // imageRef = image;
        setImageRef(image)
    };
    const onCropComplete = crop => {
        makeClientCrop(crop);
    };

    const onCropChange = (crop, percentCrop) => {
        // You could also use percentCrop:
        // this.setState({ crop: percentCrop });
        setState(state => ({ ...state, crop: crop }));
    };

    const makeClientCrop = async (crop) => {
        if (imageRef && crop.width && crop.height) {
            const croppedImageUrl = await getCroppedImg(
                imageRef,
                crop,
                'newFile.jpeg'
            );
            setState(state => ({ ...state, croppedImageUrl: croppedImageUrl }));
            props.setImageUrl(current => {
                return { ...current, image: croppedImageUrl }
            })
        }
    }
    const getCroppedImg = (image, crop, fileName, fileUrl) => {
        const canvas = document.createElement('canvas');
        const scaleX = image.naturalWidth / image.width;
        const scaleY = image.naturalHeight / image.height;
        canvas.width = crop.width;
        canvas.height = crop.height;
        const ctx = canvas.getContext('2d');

        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            crop.width,
            crop.height
        );

        return new Promise((resolve, reject) => {
            canvas.toBlob(blob => {
                if (!blob) {
                    //reject(new Error('Canvas is empty'));
                    console.error('Canvas is empty');
                    return;
                }
                blob.name = fileName;
                window.URL.revokeObjectURL(fileUrl);
                fileUrl = window.URL.createObjectURL(blob);
                resolve(fileUrl);
            }, 'image/jpeg');
        });
    }

    return (
        <div className="App">
            <div style={{ height: 380, overflowY: 'scroll' }}>
                {state.src && (
                    <ReactCrop
                        src={state.src}
                        crop={state.crop}
                        ruleOfThirds
                        onImageLoaded={onImageLoaded}
                        onComplete={onCropComplete}
                        onChange={onCropChange}
                    />
                )}
                {state.croppedImageUrl && (
                    <img alt="Crop" style={{
                        maxWidth: '100%',
                        maxHeight: 100
                    }} src={state.croppedImageUrl} />
                )}
            </div>
            <div>
                <input style={{ outline: 'none' }} type="file" accept="image/*" onChange={onSelectFile} />
            </div>
        </div>
    )
}

export default Samo
